<template>
    <div class="product-info">
        <div class="product-info">
            <h2 class="title">{{product.name}}</h2>

            <h3 class="price">Tk. {{product.selling_price}}<span>Tk. {{product.regular_price}}</span></h3>
            <p class="info-text">{{product.short_description}}</p>
            <div class="row">
                <div class="col-lg-4 col-md-4 col-12">
                    <div class="form-group color-option">
                        <label class="title-label" for="size">Choose color</label>
                        <div class="single-checkbox checkbox-style-1">
                            <input type="checkbox" id="checkbox-1" checked>
                            <label for="checkbox-1"><span></span></label>
                        </div>
                        <div class="single-checkbox checkbox-style-2">
                            <input type="checkbox" id="checkbox-2">
                            <label for="checkbox-2"><span></span></label>
                        </div>
                        <div class="single-checkbox checkbox-style-3">
                            <input type="checkbox" id="checkbox-3">
                            <label for="checkbox-3"><span></span></label>
                        </div>
                        <div class="single-checkbox checkbox-style-4">
                            <input type="checkbox" id="checkbox-4">
                            <label for="checkbox-4"><span></span></label>
                        </div>
                    </div>
                </div>
                <div class="col-lg-4 col-md-4 col-12">
                    <div class="form-group">
                        <label for="color">Battery capacity</label>
                        <select class="form-control" id="color">
                            <option>5100 mAh</option>
                            <option>6200 mAh</option>
                            <option>8000 mAh</option>
                        </select>
                    </div>
                </div>
                <div class="col-lg-4 col-md-4 col-12">
                    <div class="form-group quantity">
                        <label for="color">Quantity</label>
                        <select class="form-control">
                            <option>1</option>
                            <option>2</option>
                            <option>3</option>
                            <option>4</option>
                            <option>5</option>
                        </select>
                    </div>
                </div>
            </div>
            <div class="bottom-content">
                <div class="row align-items-end">
                    <div class="col-lg-4 col-md-4 col-12">
                        <div class="button cart-button">
                            <button class="btn" style="width: 100%;">Add to Cart</button>
                        </div>
                    </div>
                    <div class="col-lg-4 col-md-4 col-12">
                        <div class="wish-button">
                            <button class="btn"><i class="lni lni-reload"></i> Compare</button>
                        </div>
                    </div>
                    <div class="col-lg-4 col-md-4 col-12">
                        <div class="wish-button">
                            <button class="btn"><i class="lni lni-heart"></i> To Wishlist</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from "axios";

    export default {
        name: "ProductInfo",
        data(){
            return {
                id: this.$route.params.id,
                product: {},
            }
        },
        methods: {
            getProductInfo()
            {
                axios.get("http://localhost/ecom-batch-9/public/api/get-product-info/"+this.id).then((response)=>{
                    this.product = response.data;
                });
            }
        },
        created() {
            this.getProductInfo();
        }

    }
</script>

