<template>
    <div class="product-detail">
        <div class="row">
            <div class="col-12">
                <div class="info-body custom-responsive-margin" v-html="product.long_description"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from "axios";

    export default {
        name: "ProductDetail",
        data(){
            return {
                id: this.$route.params.id,
                product: {},
            }
        },
        methods: {
            getProductInfo()
            {
                axios.get("http://localhost/ecom-batch-9/public/api/get-product-info/"+this.id).then((response)=>{
                    this.product = response.data;
                });
            }
        },
        created() {
            this.getProductInfo();
        }
    }
</script>

