<template>
    <div class="product-image">
        <div class="product-images">
            <main id="gallery">
                <div class="main-img">
                    <img :src="firstImage" id="current" alt="#" height="400">
                </div>
                <div class="images" >
                    <img v-for="(productImage, key) in productImages" :key="key" :src="productImage.image" alt="#">
                </div>
            </main>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    export default {
        name: "ProductImage",
        data(){
            return {
                id: this.$route.params.id,
                productImages: [],
                firstImage: '',
            }
        },
        methods: {
            getProductImage()
            {
                axios.get("http://localhost/ecom-batch-9/public/api/get-product-images/"+this.id).then((response) =>{
                    this.productImages = response.data;
                    this.firstImage = response.data[0].image;
                });
            }
        },
        created() {
            this.getProductImage();
        }
    }
</script>

